<template>
  <div class="d-flex flex-column align-items-center">
    <br />
    <h1 class="my-5">Sube un nuevo Tatuaje</h1>

    <form
      class="d-flex flex-column align-items-center"
      @submit.prevent="agregarTatuaje()"
      enctype="multipart/form-data"
    >
      <div id="form" class="px-5 pb-3 m-0">
        <h3 class="mt-3">Nuevo tatuaje</h3>
        <input
          type="text"
          class="form-control my-2"
          placeholder="Nombre"
          v-model="tatuaje.nombre"
          required
        />
        <div class="d-flex justify-content-center mt-3">
          <p>Tipo de tatuaje</p>
          <div class="form-check mx-5">
            <input
              class="form-check-input"
              type="radio"
              name="flexRadioDefault"
              id="noTatuado"
              value="true"
              v-model="tatuaje.tatuado"
              required
            />
            <label class="form-check-label" for="noTatuado">No tatuado</label>
          </div>

          <div class="form-check">
            <input
              class="form-check-input"
              type="radio"
              name="flexRadioDefault"
              id="siTatuado"
              value="false"
              v-model="tatuaje.tatuado"
              required
            />
            <label class="form-check-label" for="siTatuado">
              Ya tatuado
            </label>
          </div>
        </div>

        <div class="form-group m-2">
          <label for="selectAutores">Autores</label>
          <select
            class="form-control"
            id="selectAutores"
            v-model="tatuaje.autor"
          >
            <option value="">Desconocido</option>
            <option
              v-for="item in autores"
              v-bind:key="item.id"
              :value="item._id"
            >
              {{ item.nombre }}
            </option>
          </select>
        </div>

        <div class="d-flex align-items-center">
          Foto de diseño
          <input
            id="imagenes"
            type="file"
            class="form-control m-2"
            @change="agregarFile"
            required
          />
        </div>
        Precio
        <input
          type="number"
          class="form-control my-2"
          placeholder="Precio"
          v-model="tatuaje.precio"
          required
        />
        Descripción
        <textarea
          class="form-control my-2"
          placeholder="Descripción"
          v-model="tatuaje.aboutTattoo"
        />
      </div>

      <b-button id="enviar" pill class="my-4" type="submit">Enviar</b-button>
    </form>

    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tatuajes: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      tatuaje: {
        nombre: '',
        tatuado: Boolean,
        autor: '',
        imagenes: Array(),
        imagen: null,
        precio: 0,
        aboutTattoo: '',
      },
      autores: [],
      selected: '',
      idTem: '',
    }
  },
  beforeCreate() {
    if (localStorage.getItem('tatuador') != 'true') {
      this.$router.go(-1)
    }
  },
  created() {
    this.axios
      .get('/usuarios/tatuadores')
      .then((res) => {
        this.autores = res.data
      })
      .catch((e) => {
        console.log(e)
      })
  },
  alertaArchivoNulo() {
    this.mensaje.color = 'warning'
    this.mensaje.texto = 'La foto debe ser en formato .jpg .png o .jpeg'
    this.showAlert()
  },
  methods: {
    alertaTrue() {
      this.mensaje.color = 'success'
      this.mensaje.texto = 'Tatuaje agregado con exito! 👌'
      this.showAlert()
    },
    alertaFalse() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'Error :('
      this.showAlert()
    },
    agregarTatuaje() {
      const formdata = new FormData()
      formdata.append('imagen', this.tatuaje.imagen)
      this.axios
        .post('/tatuajes/agregarFoto', formdata)
        .then((res) => {
          this.tatuaje.imagenes[0] = res.data
          this.alertaTrue()
        })
        .catch((e) => {
          console.log(e)
          this.alertaFalse()
        })
        .finally((e) => {
          this.axios
            .post('/tatuajes/agregar', this.tatuaje)
            .then((res) => {
              this.tatuajes.push(res.data)
              this.idTem = res.data._id
              this.alertaTrue()
            })
            .catch((e) => {
              console.log(e.response)
              this.alertaFalse()
            })
            .finally((e) => {
              this.tatuaje.nombre = ''
              this.tatuaje.tatuado = false
              this.tatuaje.autor = ''
              this.tatuaje.precio = ''
              this.tatuaje.aboutTattoo = ''
            })
        })
    },
    agregarFile(e) {
      var file = e.target.files[0]
      const types = ['image/jpg', 'image/jpeg', 'image/png']
      if (types.includes(file.type)) {
        this.tatuaje.imagen = file
      } else {
        this.alertaArchivoNulo()
        e.target.value = ''
      }
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
      // console.log(this.mensaje.color);
      if (this.mensaje.color == 'success') {
        setTimeout(() => {
          window.location.href = '/editar?id=' + this.idTem
        }, 5000)
      }
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
  },
}
</script>

<style>
h1 ~ form > div#form {
  max-width: 1000px;
  background-color: #82a5c7;
  color: #243241;
  border-radius: 10px;
}
input {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
#selectAutores {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
textarea {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
#enviar {
  width: 641px;
  background-color: #82a5c7;
  color: #243241;
}
</style>
